import { useStore } from "vuex";
import { onMounted, watch } from "vue";
export default {
  name: "notification",
  setup() {
    // vuex
    const store = useStore();

    // 
    onMounted(() => {
      // 
      const box = document.querySelector(".box");
      // 
      const callback = obj => {
        // 
        if (obj[0].removedNodes.length != 0 && document.querySelector(".boxitem")) {
          // +10
          for (let v of document.querySelectorAll(".boxitem")) {
            v.style.top = v.offsetTop - document.querySelector(".boxitem").offsetTop + 10 + "px";
          }
          // 2.8
          // callback
          document.querySelector(".boxitem").classList.add("boxitemhide");
          setTimeout(() => {
            box.removeChild(document.querySelector(".boxitemhide"));
          }, 2800);
        } else if (obj[0].removedNodes.length != 0 && !document.querySelector(".boxitem")) {
          // 
          box.style.display = "none";
        }
      };
      // 
      const observer = new MutationObserver(callback);
      observer.observe(box, {
        childList: true // 
      });

      // vuex
      const createItem = () => {
        // vuex
        let noticestr = store.state.noticeContent;
        // li
        let newitem = document.createElement("li");
        // html
        let htmlstr = `<span class="iconfont icon-prompt"></span>
      <span class="itemtitle"></span>
      <p class="itemcontent">
        ${noticestr}
      </p>`;
        // 
        box.appendChild(newitem);
        // 
        newitem.innerHTML = htmlstr;
        // 
        newitem.classList.add("boxitem");
        // 
        let boxtotalheight = 0;
        // 
        // 10
        for (let v of box.children) {
          boxtotalheight += v.offsetHeight + 10;
        }
        // 
        newitem.style.top = boxtotalheight + "px";
        // 
        newitem.style.display = "block";
        // vuexvuex
        // vuexwatch
        store.commit("setNoticeContent", "");
        // 
        if (box.children.length == 1) {
          // 
          setTimeout(() => {
            // 
            document.querySelector(".boxitem").classList.add("boxitemhide");
            // 
            // callback
            setTimeout(() => {
              box.removeChild(document.querySelector(".boxitemhide"));
            }, 2800);
          }, 300);
        }
      };

      // vuex
      watch(() => store.state.noticeContent, () => {
        // 
        if (store.state.noticeContent != "") {
          // 
          box.style.display = "block";
          createItem();
        }
      });
    });
    return {};
  }
};