import { FullScreen } from "@element-plus/icons-vue";
import screenfull from "screenfull";
import { useStore } from "vuex";
export default {
  name: "screenmode",
  components: {
    FullScreen
  },
  setup() {
    const store = useStore();
    const changeMode = () => {
      if (screenfull.isEnabled) {
        let screen = window.screen;
        let body = document.body.getBoundingClientRect();
        let screenmode = screen.height === body.height && screen.width === body.width;
        if (screenmode == true) {
          screenfull.exit();
        } else {
          screenfull.request();
          store.commit("setNoticeContent", "f12f11");
        }
      } else {
        console.log("");
      }
    };
    return {
      changeMode
    };
  }
};